import React, { Component } from 'react';
import { renderRoutes } from 'react-router-config';

export default class Main extends Component {
  render() {
    let { routes } = this.props.route
    return (
      <div className="main">
        { renderRoutes(routes) }
      </div>
    )
  }
}



