import store from 'store';
import _Global from '../libs/index.js';
export default {
    getDefaultProps() {
        return {
            _Global,
            _isAuth () {
                let UserInfo = store.get('userInfo')
                let to = ''
                if (UserInfo) {
                    if (UserInfo.role === 'genuis') {
                        to = '/main/genuisinfo'
                    } else if (UserInfo.role === 'boss') {
                        to = '/main/bossiofo'
                    }
                }
                return to
            }
        }
    },
    componentWillMount() {

    },
    componentDidMount() {

    },
    setInterval() {

    },
    componentWillUnmount() {

    }
}



