import React from 'react';
import reactMixin from 'react-mixin';
import axios from 'axios';
import store from 'store';
import { withRouter, Redirect } from 'react-router-dom';
import { Button, WhiteSpace, WingBlank, List, InputItem, Modal } from 'antd-mobile';
import Logo from '../../components/Logo/index';
import mixins from '../..//mixins/index';

let showAlert = ({ title = '提示', context = '' }) => {
    let alertInstance = Modal.alert(title, context, [
        { text: 'OK', onPress: () => { } },
    ]);
    setTimeout(() => {
        // 可以调用close方法以在外部close
        alertInstance.close();
    }, 2000);
}

@withRouter
@reactMixin.decorate(mixins)
class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            password: ""
        }
        this.register = this.register.bind(this);
    }
    register() {
        this.props.history.push("/register");
    }
    handleChange(key, val) {
        this.setState({
            [key]: val
        });
    }
    Login() {
        let _this = this;
        let { name, password } = this.state;
        if (name === "") {
            showAlert({ context: "用户名不得为空！" });
            return
        }
        if (password === "") {
            showAlert({ context: "密码不得为空！" });
            return
        }
        axios.post(this.props._Global.config.apiBasicUrl + this.props._Global.api.login.login, { name, password }).then((res) => {
            if (res.code === "0") {
                //把登录信息写入本地
                store.set('userInfo', res.data);
                if (res.data.role === "boss") {
                    _this.props.history.push("/main/bossiofo");
                } else {
                    _this.props.history.push("/main/genuisinfo");
                }
            } else {
                showAlert({ context: '登录失败，请重试！' });
            }
        });
    }

    render() {
        let url = this.props._isAuth()
        return url ? (<Redirect from="/login" to={ url }></Redirect>) : (
            <React.Fragment>
                <Logo></Logo>
                <WhiteSpace />
                <List>
                    <InputItem onChange={(v) => this.handleChange("name", v)}>用户名</InputItem>
                    <InputItem onChange={(v) => this.handleChange("password", v)} type="password">密码</InputItem>
                </List>
                <WhiteSpace />
                <WingBlank>
                    <Button type="primary" onClick={() => this.Login()}>登录</Button><WhiteSpace />
                    <Button type="primary" onClick={this.register}>注册</Button>
                </WingBlank>
            </React.Fragment>
        )
    }
}

export default Login;


