import React from 'react';
import { Redirect } from 'react-router-dom';
import reactMixin from 'react-mixin';
import axios from 'axios';
import { Button, WhiteSpace, WingBlank, List, InputItem, Radio, Modal } from 'antd-mobile';
import mixins from '../../mixins/index';
import Logo from '../../components/Logo/index';

//弹框
const alert = Modal.alert;
const showAlert = ({title = '提示', context = ''}) => {
    const alertInstance = alert(title, context, [
        { text: 'OK', onPress: () => {} },
    ]);
    setTimeout(() => {
        // 可以调用close方法以在外部close
        alertInstance.close();
    }, 2000);
};

@reactMixin.decorate(mixins)
class Register extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            password: "",
            repeatPassword: "",
            role: "genuis"
        }
    }
    Login() {
        this.props.history.push("/login");
    }
    handleChange(key, val) {
        this.setState({
            [key]: val
        });
    }
    registerUserInfo() {
        let _this = this;
        let { name, password, repeatPassword, role } = this.state;
        if (password === repeatPassword) {
            axios.post(this.props._Global.config.apiBasicUrl + this.props._Global.api.register.registerUserInfo, { name, password, repeatPassword, role }).then((res) => {
                if (res.code === "0") {
                    _this.props.history.push("/login");
                } else {
                    showAlert({context : res.msg});
                }
            });
        } else {
            showAlert({context : '密码有误！'});
        }
    }
    render() {
        const RadioItem = Radio.RadioItem;
        let url = this.props._isAuth()
        return url ?  (<Redirect from="/register" to={ url }></Redirect>) : (
            <React.Fragment>
                <Logo></Logo>
                <WhiteSpace />
                <List>
                    <InputItem onChange={(v) => this.handleChange("name", v)}>用户</InputItem>
                    <InputItem onChange={(v) => this.handleChange("password", v)} type="password">密码</InputItem>
                    <InputItem onChange={(v) => this.handleChange("repeatPassword", v)} type="password">确认密码</InputItem>
                    <RadioItem checked={this.state.role === "genuis"} onChange={() => this.handleChange("role", "genuis")}>牛人</RadioItem>
                    <RadioItem checked={this.state.role === "boss"} onChange={() => this.handleChange("role", "boss")}>BOSS</RadioItem>
                </List>
                <WhiteSpace />
                <WingBlank>
                    <Button type="primary" onClick={() => this.registerUserInfo()}>注册</Button><WhiteSpace />
                    <Button type="primary" onClick={() => this.Login()}>登录</Button>
                </WingBlank>
            </React.Fragment>
        )
    }
}

export default Register;


